<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <div class="text-right">
            <!-- <button class="btn btn-primary mt-4" data-toggle="modal" data-target="#productModal">建立新的產品</button> -->
            <button class="btn btn-primary mt-4" @click="openModal(true)">建立新的產品</button>
        </div>
        <tabletemp 
        :products="products"
        :pagination="pagination" 
        :columns="columns"
        :deletebtn="true"
        :editbtn="true"
        @clicledit="clicledit"
        @clicldelete="deletproduct"
        @clickpage="clickpage">
        </tabletemp>
        
        <modal v-if="openclick"
        :modaltype="1"
        :title="'產品'"
        :type="'product'"
        :modalname="modalname"
        :openclick="openclick"
        :temp="temp"
        :isNew="isNew"
        @closeclick="closeModal">
        </modal >
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ temp.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger" @click="deleteconfirm"
                    >確認刪除</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import tabletemp from '@/components/share/table.vue';
import modal from '@/components/share/modal.vue';
export default {
    components:{
        tabletemp,
        modal
    },
    data(){
        return{
            products:[],
            temp:{},
            isNew:false,
            isLoading:false,
            status:{
                fileUploading:false,
            },
            pagination:{},
            modalname:[
                "輸入圖片網址",
                "或 上傳圖片",
                "標題",
                "分類",
                "單位",
                "原價",
                "售價",
                "產品描述",
                "說明內容",
                "是否啟用",
                "標題",
                "分類",
                "單位",
                "原價",
                "售價",
                "產品描述",
                "說明內容",
                "是否啟用",
            ],
            columns: [
                {
                label: '分類',
                field: 'category',
                },
                {
                label: '產品名稱',
                field: 'title',
                },
                {
                label: '原價',
                field: 'origin_price',
                },
                {
                label: '售價',
                field: 'price',
                },
                {
                label: '是否啟用',
                field: 'is_enabled',
                display:{
                    1: '啟用',
                    },
                },
            ],
            openclick:false,
            deleteitem:{},
            tabledata:[],
        }
    },
    methods:{
        getproducts(page=1){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/admin/products?page='+page;
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((res)=>{
                // console.log(res.data);
                vm.pagination=res.data.pagination;
                vm.products=res.data.products;
                vm.tablecontent = res.data;
                vm.isLoading = false;
            })
        },
        openModal(isNew,item){
            if(isNew){
                this.temp={};
                this.isNew=true;
            }
            else{
                this.temp=Object.assign({},item);//ES6語法，可以把item放進新的空陣列，避免產生參考特性
                this.isNew=false;
            }
            this.openclick=true;
        },
        closeModal(click,type){
            const vm = this;              
            vm.getproducts();
            if(click!="cancel"){
                this.$bus.$emit('message:push',click,type);
            }
            vm.openclick=false;
        },
        clickpage(page){   
            const vm = this;           
            vm.getproducts(page);
            vm.openclick=false;
        },
        clicledit(item,isNew){
            this.temp=Object.assign({},item);//ES6語法，可以把item放進新的空陣列，避免產生參考特性
            this.openclick=true;
        },
        deletproduct(item){
            this.deleteitem=item;
            $('#delProductModal').modal('show');
        },
        deleteconfirm(){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/admin/product/'+this.deleteitem.id;
            const vm = this;
            this.$http.delete(api,this.deleteitem).then((res)=>{
                if(res.data.success){
                    $('#delProductModal').modal('hide');
                    vm.getproducts();
                }
                else{
                    $('#delProductModal').modal('hide');
                    vm.getproducts();
                    this.$bus.$emit('message:push',res.data.message,'danger');
                }
            })
        }
    },
    computed:{
        getpagination(){
            const vm = this ;
            return vm.pagination;
        },
    },
    created(){
        this.getproducts();
        
    }
}
</script>