<template>
    <div>
        <table class="table m-4">
            <thead>
                <tr>
                    <th width="120" v-for="itemname in columns" :key="itemname.field">{{itemname.label}}</th>
                    <th width="120" v-if="editbtn||deletebtn">編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="itemproducts in getorder" :key="itemproducts.id">
                    <td v-for="itemcolumns in columns" :key="itemcolumns.field">
                        <!-- {{itemcolumns}}   -->
                        <template v-if="itemcolumns.field == 'price'">
                            {{itemproducts[itemcolumns.field] | currency}}
                        </template>
                        <template v-else-if="itemcolumns.field == 'origin_price'">
                            {{itemproducts[itemcolumns.field] | currency}}
                        </template>
                        <template v-else-if="itemcolumns.field == 'total'">
                            {{itemproducts[itemcolumns.field] | currency}}
                        </template>
                        <template v-else-if="itemcolumns.field=='is_enabled'">
                            <template v-if="itemproducts.is_enabled">
                                啟用
                            </template>
                            <template v-else>
                                不啟用
                            </template>
                        </template>
                        <template v-else-if="itemcolumns.field=='is_paid'">
                            <template  v-if="!itemproducts.is_paid">
                                <router-link class="text-danger" :to="`/customer_checkout/${itemproducts.id}`">未付款
                                </router-link>
                            </template>
                            <template  v-else>
                                <router-link class="text-success" :to="`/customer_checkout/${itemproducts.id}`">已付款
                                </router-link>
                            </template>
                        </template>
                        <template v-else>
                            {{itemproducts[itemcolumns.field]}}
                        </template>
                    </td>  
                    <td v-if="editbtn||deletebtn"> 
                        <button v-if="editbtn" class="btn btn-outline-primary btn-sm" @click="openModal(false,itemproducts)">編輯</button>
                        <button v-if="deletebtn" class="btn btn-outline-danger btn-sm" @click="deletproduct(itemproducts)" >刪除</button>
                    </td>           
                </tr>
            </tbody>
        </table> 
        <page class="d-flex justify-content-center" :paginationdata="pagination" @clickpage="getpage"></page>
    </div>
</template>

<script>
import $ from 'jquery';
import page from '@/components/share/page.vue';
export default {
    components:{
        page
    },
    props:{
        products:{
            type:Array
        },
        pagination:{
            type:Object
        },
        columns:{
            type:Array
        },
        deletebtn:{
            type:Boolean,
            default:false,
        },
        editbtn:{
            type:Boolean,
            default:false,
        },
        type:{
            type:String,
            default:'product',
        }
    },
    data(){
        return{
            isNew:true,
            tempproduct:{},
            openclick:false,
            orderitem:{},
            orderitems:[]
        }
    },
    methods:{
        openModal(isNew,item){
            this.tempproduct=Object.assign({},item);//ES6語法，可以把item放進新的空陣列，避免產生參考特性
            this.isNew=false;
            this.openclick=true;
            this.$emit("clicledit",this.tempproduct,this.isNew);
        },
        deletproduct(item){
            this.$emit("clicldelete",item);
        },
        getpage(page){
            this.$emit("clickpage",page);
        },
    },
    computed:{
        //取得訂單資料
        getorder(){
            if(this.type=='product'){
                let vm= this;
                    let convertDate=""
                    vm.products.forEach((item,index) => {
                        if(vm.products){
                            convertDate = new Date(item.due_date  * 1000);
                            item.due_date= convertDate.getFullYear() +"/"+(convertDate.getMonth()+1) +"/"+convertDate.getDate();
                        }
                    });
                return this.products;
            }
            else{
                // console.log(item);
                let vm= this;
                let titlename="";
                let convertDate=""
                    // console.log(vm.products);
                vm.products.forEach((item,index) => {
                    // console.log(item);
                    convertDate = new Date(item.create_at  * 1000);
                    vm.orderitem['create_at']= convertDate.getFullYear() +"/"+(convertDate.getMonth()+1) +"/"+convertDate.getDate();
                    vm.orderitem['email']=item.user.email;
                    vm.orderitem['total']=item.total;
                    vm.orderitem['is_paid']=item.is_paid;
                    vm.orderitem['id']=item.id;
                    if(item.products){
                        Object.keys(item.products).forEach((productitem) => {
                            titlename+=item.products[productitem].product.title + ':' + item.products[productitem].qty + item.products[productitem].product.unit+',';
                        });
                        vm.orderitem['content']=titlename;
                    } 
                    vm.orderitems.push(vm.orderitem);
                    titlename="";
                    vm.orderitem={};
                });
                // console.log(vm.orderitems);
                return vm.orderitems;
            }
        }
    },
}
</script>

