<template>
  <div>
      <loading :active.sync="isLoading" ></loading>
      <tabletemp 
        :columns="columns"
        :products="orders"
        :pagination="pagination" 
        :type="'order'">
      </tabletemp>
  </div>
</template>

<script>
import tabletemp from '@/components/share/table.vue';
export default {
  components:{
    tabletemp
  },
  data(){
    return{
      isLoading:false,
      orders:[],
      pagination:{},
      columns: [
          {
          label: '購買時間',
          field: 'create_at',
          },
          {
          label: 'Email',
          field: 'email',
          },
          {
          label: '購買款項',
          field: 'content',
          },
          {
          label: '應付金額',
          field: 'total',
          },
          {
          label: '是否付款',
          field: 'is_paid',
          display:{
              1: '啟用',
              },
          },
      ],
    }
  },
  methods:{
    getorder(page=1){
      const api ='https://vue-course-api.hexschool.io/api/irene0120/admin/orders?page='+page;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((res)=>{
          // console.log(res.data);
          vm.pagination=res.data.pagination;
          vm.orders=res.data.orders;
          vm.isLoading = false;
      })
    },
  },
  created(){
        this.getorder();
        
    }
}
</script>

