<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <strong>購物車</strong>
        <table class="table mt-4 " style="width:50%;margin-left:25%"  >
            <thead>
                <tr >
                    <th width="80"></th>
                    <th width="120">品名</th>
                    <th width="80">數量</th>
                    <th width="80">單價</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in shopping.carts" :key="item.id">
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="deletesingle(item.id)">刪除</button>
                    </td>
                    <td>{{item.product.category}}</td>
                    <td class="text-right">
                        {{item.qty}}/{{item.product.unit}}
                    </td>
                    <td class="text-right">
                        {{item.final_total | currency}}
                    </td>
                </tr>
            </tbody>
            <tfoot style="text-align:end">
                <tr>
                    <td colspan="3">
                        <span>總計</span>
                    </td>
                    <td>
                        {{shopping.total | currency}}
                    </td>
                </tr>
                <tr v-if="shopping.final_total !==shopping.total">
                    <td colspan="3" class="text-right text-success">
                        <span>折扣價</span>
                    </td>
                    <td class="text-right text-success">
                        {{shopping.final_total | currency}}
                    </td>
                </tr>
            </tfoot>
        </table>
        <div style="width:50%;margin-left:25%" class="input-group mb-3">
            <input  type="text" v-model="coupon_code" class="form-control" placeholder="請輸入優惠碼" aria-label="Recipient's username" aria-describedby="button-addon2">
            <div class="input-group-append">
                <button class="btn btn-outline-secondary" @click="addcouponcode" type="button" id="button-addon2">套用優惠碼</button>
            </div>
        </div>
        <div class="modal" tabindex="-1" role="dialog" id="productModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{product.title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div style="height: 350px; background-size: cover; background-position: center"
                     :style="{backgroundImage:`url(${product.imageUrl})`}">
                    </div>
                    <div class="mt-4">
                        {{product.content}}
                    </div>
                    <div class="d-flex justify-content-between align-itema-baseline">
                        <div class="h4" v-if="!product.price">{{product.origin_price}} 元</div>
                        <del class="h6" v-if="product.price">{{product.origin_price}} 元</del>
                        <div class="h4" v-if="product.price">{{product.price}} 元</div>
                    </div>
                    <select name="" class="from-control mt-3" v-model="product.num">
                        <option value="" disabled selected>請選購</option>
                        <option :value="num" v-for="num in 10" :key="num">
                            選購{{num}}{{product.unit}}
                        </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowarp mr-3">
                        小計<strong>{{product.num * product.price}}</strong>元
                    </div>
                    <button type="button" @click="addtoCart(product.id,product.num)" class="btn btn-secondary" data-dismiss="modal">
                        <!-- <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i> -->
                        <i class="fas fa-spinner fa-spin" ></i>
                        加到購物車
                    </button>
                </div>
                </div>
            </div>
        </div>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group" :class="{ 'form-group--error': $v.form.user.email.$error }" >
                    <label for="useremail">Email</label>
                        <input type="text" class="form-control" name="Email" id="useremail"
                            v-model="form.user.email"  placeholder="請輸入 Email" @blur="$v.form.user.email.$touch()">
                </div>
                <div style="margin-bottom:10px" class="error text-danger" v-if="$v.form.user.email.$error && !$v.form.user.email.required">Email欄位不得留空</div>
                
                <div class="form-group" :class="{ 'form-group--error': $v.form.user.name.$error }" >
                    <label for="username">收件人姓名</label>
                        <input type="text" class="form-control" name="name" id="username"
                            v-model ="form.user.name"  placeholder="輸入姓名" @blur="$v.form.user.name.$touch()"   >
                </div>
                <div style="margin-bottom:10px" class="error text-danger" v-if="$v.form.user.name.$error && !$v.form.user.name.required"  >姓名欄位不得留空</div>
                
                <div class="form-group" :class="{ 'form-group--error': $v.form.user.tel.$error }" >
                    <label for="usertel">收件人電話</label>
                        <input type="text" class="form-control" name="tel" id="usertel"
                            v-model="form.user.tel"  placeholder="請輸入電話" @blur="$v.form.user.tel.$touch()">
                </div>
                <div style="margin-bottom:10px" class="error text-danger" v-if="$v.form.user.tel.$error && !$v.form.user.tel.required">電話欄位不得留空</div>
                
                <div class="form-group" :class="{ 'form-group--error': $v.form.user.address.$error }" >
                    <label for="useraddress">收件人地址</label>
                        <input type="text" class="form-control" name="address" id="useraddress"
                            v-model="form.user.address"  placeholder="請輸入地址" @blur="$v.form.user.address.$touch()">
                </div>
                <div style="margin-bottom:10px" class="error text-danger" v-if="$v.form.user.address.$error &&!$v.form.user.address.required">地址欄位不得留空</div>
                
                <div class="form-group">
                <label for="comment">留言</label>
                <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                <button class="btn btn-danger" >送出訂單</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import { required, minLength, between } from 'vuelidate/lib/validators'
export default {
    data(){
        return{
            products:[],
            isLoading:false,
            product:{},
            status:{
                loadingItem:'',
            },
            shopping:{},
            coupon_code:"",
            form:{
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:''
                }
            }
        }
    },
    validations: {
        form:{
            user:{
                name:{
                    required,
                },
                email:{
                    required,
                },
                tel:{
                    required,
                },
                address:{
                    required,
                },
            }
        }
    },
    methods:{
        getCart(){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/cart';
            const vm = this;
            vm.isLoading=true;
            this.$http.get(api).then((res)=>{
                console.log(res.data.data);
                vm.isLoading=false;
                vm.shopping=res.data.data;
            })
        },
        deletesingle(id){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/cart/'+id;
            const vm = this;
            vm.isLoading=true;
            this.$http.delete(api).then((res)=>{
                if(res.data.success){
                    vm.getCart();
                }
                else{
                    vm.getCart();
                }
                vm.isLoading=false;
            })
        },
        addcouponcode(){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/coupon/';
            const vm = this;
            const code ={
                code:vm.coupon_code
            }
            vm.isLoading=true;
            this.$http.post(api,{data:code}).then((res)=>{
                if(res.data.success){
                    vm.getCart();
                    this.$bus.$emit('message:push',res.data.message,'danger');
                }
                else{
                    vm.getCart();
                    this.$bus.$emit('message:push',res.data.message,'success');
                }
                vm.isLoading=false;
            })
        },
        createOrder(){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/order/';
            const vm = this;
            const order =vm.form;
            vm.isLoading=true;
            this.$http.post(api,{data:order}).then((res)=>{
                console.log('訂單已完成',res);
                if(res.data.success){
                    vm.$router.push(`/customer_checkout/${res.data.orderId}`)
                    // vm.getCart();
                    this.$bus.$emit('message:push',res.data.message,'success');
                }
                else{
                    vm.getCart();
                    this.$bus.$emit('message:push',res.data.message,'danger');
                }
                vm.isLoading=false;
            })
        },
    },
    computed:{
    },
    created(){
        this.getCart();
    }
}
</script>

