<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id" >
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center"
                     :style="{backgroundImage:`url(${item.imageUrl})`}">
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{item.title}}</a>
                    </h5>
                    <p class="card-text">{{item.content}}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h5" v-if="!item.price">{{item.origin_price}}</div>
                        <del class="h6" v-if="item.price">{{item.origin_price}}</del>
                        <div class="h5" v-if="item.price">{{item.price}}</div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" @click="getproduct(item.id)" class="btn btn-outline-secondary btn-sm">
                        <i class="fas fa-spinner fa-spin" v-if="item.id === status.loadingItem"></i>
                        查看更多
                    </button>
                    <button type="button" @click="addtoCart(item.id)" class="btn btn-outline-danger btn-sm ml-auto">
                        <i class="fas fa-spinner fa-spin" v-if="item.id === status.loadingItem"></i>
                        加到購物車
                    </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" tabindex="-1" role="dialog" id="productModal">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{product.title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div style="height: 350px; background-size: cover; background-position: center"
                     :style="{backgroundImage:`url(${product.imageUrl})`}">
                    </div>
                    <div class="mt-4">
                        {{product.content}}
                    </div>
                    <div class="d-flex justify-content-between align-itema-baseline">
                        <div class="h4" v-if="!product.price">{{product.origin_price}} 元</div>
                        <del class="h6" v-if="product.price">{{product.origin_price}} 元</del>
                        <div class="h4" v-if="product.price">{{product.price}} 元</div>
                    </div>
                    <select name="" class="from-control mt-3" v-model="product.num" @change="onChangeNum()">
                    <!-- <select class="from-control mt-3" v-model="product.num" > -->
                        <template :value="num" v-for="num in 10" >
                            <option v-if="num==0" :value="num" disabled :key="num" selected> 請選擇</option>
                            <option v-else :value="num" :key="num">
                                選購{{num}}{{product.unit}}
                            </option>
                        </template>
                        <!-- <option value=0 disabled selected> 請選擇</option>
                        <option :value="num" v-for="num in 10" :key="num">
                            選購{{num}}{{product.unit}}
                        </option> -->
                        <!-- <option value="0" disabled selected> 請選擇</option> -->
                        <!-- <option :value="num" v-for="num in 10" :key="num" @change="onChangeNum($event)" >
                            選購{{num}}{{product.unit}}
                        </option> -->
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowarp mr-3">
                        <!-- 小計 <strong>{{product.num * product.price}}</strong> 元 -->
                        小計 <strong>{{sum}}</strong> 元
                    </div>
                    <button type="button" @click="addtoCart(product.id,product.num)" class="btn btn-secondary" data-dismiss="modal">
                        <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                        <!-- <i class="fas fa-spinner fa-spin" ></i> -->
                        加到購物車
                    </button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
export default {
    data(){
        return{
            products:[],
            isLoading:false,
            product:{},
            status:{
                loadingItem:'',
            },
            shopping:{},
            sum:"",
        }
    },
    methods:{
        getproducts(page=1){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/products?page='+page;
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((res)=>{
                // console.log(res.data);
                vm.products=res.data.products;
                vm.isLoading = false;
            })
        },
        getproduct(id){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/product/'+id;
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((res)=>{
                // console.log(res.data);
                vm.product=res.data.product;
                $('#productModal').modal('show');
                vm.product.num=1;
                this.sum=vm.product.price;
                vm.status.loadingItem="";
                vm.isLoading = false;
            })
        },
        addtoCart(id,qty=1){
            const api ='https://vue-course-api.hexschool.io/api/irene0120/cart';
            const vm = this;
            vm.status.loadingItem=id;
            const cart = {
                product_id:id,
                qty
            }
            this.$http.post(api,{data:cart}).then((res)=>{
                vm.status.loadingItem='';
                $('#productModal').modal('hide');
            })
        },
        onChangeNum() {
            const vm = this;
            vm.sum=vm.product.num * vm.product.price;
        }
    },
    computed:{
    },
    watch:{
    },
    mounted(){
    },
    created(){
        this.getproducts();
    }
}
</script>

