import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '@/components/Dashboard.vue'
import Login from '@/components/pages/Login.vue'
import Products from '@/components/pages/Products.vue'
import Order from '@/components/pages/Order.vue'
import Coupon from '@/components/pages/Coupon.vue'
import CustomerOrder from '@/components/pages/CustomerOrder.vue'
import CustomerCheckout from '@/components/pages/CustomerCheckout.vue'
import ShoppingCar from '@/components/pages/ShoppingCar.vue'


Vue.use(VueRouter)

const routes = [
  {
    path:'*',
    redirect:'login',//如果導向沒有的路由則會導回login
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard,
    children:[
      {
        path: 'products',
        name: 'products',
        component: Products,
        meta: { requiresAuth: true }, //以此參數來判斷頁面是否需要驗證

      },
      {
        path: 'order',
        name: 'order',
        component: Order,
        meta: { requiresAuth: true }, //以此參數來判斷頁面是否需要驗證

      },
      {
        path: 'coupon',
        name: 'coupon',
        component: Coupon,
        meta: { requiresAuth: true }, //以此參數來判斷頁面是否需要驗證

      },
    ]
  },
  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
    children:[
      {
        path: 'customer_order',
        name: 'customerorder',
        component: CustomerOrder,
      },
      {
        path: 'customer_checkout/:orderId',
        name: 'customercheckout',
        component: CustomerCheckout,

      },
      {
        path: 'shopping_car',
        name: 'shopping_car',
        component: ShoppingCar,

      },
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
