<template>
    <div >
        <form class="form-signin" @submit.prevent="singin">
            <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
            <label for="inputEmail" class="sr-only">Email address</label>
            <input type="email" id="inputEmail" v-model="user.username" class="form-control" 
                placeholder="Email address" required autofocus>
            <label for="inputPassword" class="sr-only">Password</label>
            <input type="password" id="inputPassword" v-model="user.password" class="form-control" 
                placeholder="Password" required>
            <div class="checkbox mb-3">
                <label>
                <input type="checkbox" value="remember-me"> Remember me
                </label>
            </div>
            <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
            <p class="mt-5 mb-3 text-muted">&copy; 2017-2019</p>
        </form>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    
  },
  data(){
      return{
        user:{
            username:"",
            password:""
        }
      }
  },
  methods:{
      singin(){
        const api ='https://vue-course-api.hexschool.io/admin/signin';
        const vm = this;
        this.$http.post(api,vm.user).then((res)=>{
            if(res.data.success){
                const token = res.data.token;
                const expired = res.data.expired;
                // console.log(token,expired);
                /*一定要再轉址前存進cookies
                    hexToken=自定名稱
                    expires=需要轉成一班時間格式
                */
                document.cookie = `hexToken=${token}; expires=${new Date(expired)};`;
                vm.$router.push('/admin/products');
            }
        })
      }
  }
}
</script>

