<template>
  <div>
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
              <span>新增{{title}}</span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
              </button>
          </div>
          <div class="modal-body">
              <div class="row">
                <template v-if="modaltype==1">                
                  <div class="col-sm-6">
                    <div class="form-group">
                    <label for="image">輸入圖片網址</label>
                    <input v-model="temp.imageUrl" type="text" class="form-control" id="image"
                        placeholder="請輸入圖片連結">
                    </div>
                    <div class="form-group">
                    <label for="customFile">或 上傳圖片
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile" class="form-control"
                        ref="files" @change="uploadFile">
                    </div>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" alt="">
                    <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category">分類</label>
                            <input v-model="temp.category" type="text" class="form-control" id="category"
                            placeholder="請輸入分類">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price">單位</label>
                            <input v-model="temp.unit" type="unit" class="form-control" id="unit"
                            placeholder="請輸入單位">
                        </div>
                    </div>
                    <div class="form-row">
                        <div class="form-group col-md-6">
                          <label for="origin_price">原價</label>
                              <input v-model="temp.origin_price" type="number" class="form-control" id="origin_price"
                              placeholder="請輸入原價">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price">售價</label>
                            <input v-model="temp.price" type="number" class="form-control" id="price"
                            placeholder="請輸入售價">
                        </div>
                    </div>
                  </div>
                  <div class="col-sm-6">
                      <div class="form-group">
                        <label for="title">標題</label>
                        <input v-model="temp.title" type="text" class="form-control" id="title"
                            placeholder="請輸入標題">
                      </div>
                      <hr>
                      <div class="form-group">
                        <label for="description">產品描述</label>
                        <textarea v-model="temp.description" type="text" class="form-control" id="description"
                            placeholder="請輸入產品描述"></textarea>
                      </div>
                      <div class="form-group">
                        <label for="content">說明內容</label>
                        <textarea v-model="temp.content" type="text" class="form-control" id="content"
                            placeholder="請輸入產品說明內容"></textarea>
                      </div>
                      <div class="form-group">
                        <div class="form-check">
                            <input v-model="temp.is_enabled" class="form-check-input" type="checkbox"
                            :true-value="1"
                            :false-value="0"
                            id="is_enabled">
                            <label class="form-check-label" for="is_enabled">
                            是否啟用
                            </label>
                        </div>
                      </div>
                  </div>
                </template>
                <template v-if="modaltype==2">                
                  <div class="col-sm-12">
                    <template v-for="item in modalname" >
                      <div v-if="item.field == 'due_date'" class="form-group" :key="item.field" >
                        <label for="title">{{item.label}}</label>
                        <input v-model="temp[item.field]" type="date" class="form-control" :id="item.field" :placeholder="['請輸入'+item.label]">
                      </div>
                      <div v-else-if="item.field != 'is_enabled'" class="form-group" :key="item.field" >
                        <label for="title">{{item.label}}</label>
                        <input v-model="temp[item.field]" type="text" class="form-control" :id="item.field" :placeholder="['請輸入'+item.label]">
                      </div>
                      <div v-else class="form-group" :key="item.field">
                        <div class="form-check">
                            <input v-model="temp[item.field]" class="form-check-input" type="checkbox"
                            :true-value="1"
                            :false-value="0"
                            id="is_enabled">
                            <label class="form-check-label" for="is_enabled">
                            是否啟用
                            </label>
                        </div>
                      </div>
                    </template>  
                  </div>
                </template>
              </div>
          </div>
          <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal" @click="canelclick">取消</button>
              <button type="button" class="btn btn-primary" @click="updateproduct">確認</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
export default {
  props:{
    title:{
      type:String
    },
    type:{
      type:String
    },
    modalname:{
      type:Array
    },
    openclick:{
      type:Boolean
    },
    temp:{
      type:Object
    },
    modaltype:{
      type:Number
    },
    isNew:{
      type:Boolean
    }
  },
  data(){
    return{
      status:{
          fileUploading:false,
      },
    }
  },
  methods:{
    updateproduct(){
        const vm = this;
        let api ='https://vue-course-api.hexschool.io/api/irene0120/admin/'+vm.type;
        let httpMethod ='post'
        if(!this.isNew){
            api ='https://vue-course-api.hexschool.io/api/irene0120/admin/'+vm.type+'/'+vm.temp.id;
            httpMethod = 'put'
        }
        // console.log(vm.temp);
        //轉換到期日_轉 Unix Timestamp
        if(vm.temp.due_date!=""){
          const timestamp = Math.floor(new Date(vm.temp.due_date) / 1000); // 1579046400
          vm.temp.due_date = timestamp;
        }
        this.$http[httpMethod](api,{data:vm.temp}).then((res)=>{
            if(res.data.success){
              this.$emit("closeclick",res.data.message,'success');
            }
            else{
              this.$emit("closeclick",res.data.message,'danger');
            }
            $('#productModal').modal('hide');
        })
        
    },
    uploadFile(){
      // console.log(this);
      const uploadFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append('file-to-upload', uploadFile);
      const api ='https://vue-course-api.hexschool.io/api/irene0120/admin/upload/';
      vm.status.fileUploading = true;
      this.$http.post(api,formData,{
          headers:{
              'Content-Type':'multipart/form-data'
          }
      }).then((res)=>{
          console.log(res.data);
          if(res.data.success){
              // vm.tempproduct.imagUrl = res.data.imagUrl;
              vm.$set(vm.temp,'imageUrl',res.data.imageUrl);
              vm.status.fileUploading = false;
          }else{
              this.$bus.$emit('message:push',res.data.message,'danger');
          }
      })
    },
    canelclick(){
        this.$emit("closeclick","cancel");
    },
  },
  computed:{
    
  },
  mounted(){
    if(this.openclick){
      // alert(this.openclick);
      $('#productModal').modal('show');
    }
  },
  created(){
    // alert(this.openclick);
  }
}
</script>

