<template>
  <div>
      <nav aria-label="Page navigation example">
        <ul class="pagination">
            <li class="page-item" @click.prevent="clickpage(paginationdata.current_page - 1)" :class="{'disabled':!paginationdata.has_pre}">
            <a class="page-link" href="#" aria-label="Previous" >
                <span aria-hidden="true">&laquo;</span>
            </a>
            </li>
            <li class="page-item" @click.prevent="clickpage(page)" :class="{'active':paginationdata.current_page===page}" v-for="page in paginationdata.total_pages" :key="page">
                <a class="page-link" href="#" >
                    {{page}}
                </a
            ></li>
            <li class="page-item" @click.prevent="clickpage(paginationdata.current_page + 1)" :class="{'disabled':!paginationdata.has_next}">
            <a class="page-link" href="#" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
            </a>
            </li>
        </ul>
    </nav>
  </div>
</template>

<script>
export default {
    props:{
        paginationdata:{
            Type:Object
        }
    },
    data(){
        return{

        }
    },
    methods:{
        clickpage(page){
            this.$emit('clickpage',page);
        }
    }
}
</script>

